"use strict"
import Chart from 'chart.js/auto';

//skapar variablar av knapp elementen i html
let openButton = document.getElementById("open-menu")
let closeButton = document.getElementById("close-menu")


//skapar en eventlistener som lyssnar efter nr anvndare klickar p dessa element
openButton.addEventListener('click', toggleMenu)
closeButton.addEventListener('click', toggleMenu)
//function som kollar ifall mobilmenyn visas eller inte nr man trycker p respektive knapp, om den inte visas s visas den och vice versa. Den ndrar knappens css ifall display r none till block annars ndras den till none
function toggleMenu(){
    let mobileMenuEl = document.getElementById("mobilemenu")
    let style = window.getComputedStyle(mobileMenuEl)

    if(style.display === "none") {
        mobileMenuEl.style.display = "block";
    } else{
        mobileMenuEl.style.display = "none"
    }
}

/**
 * Anropar funktionen init nr sidan laddas in
 */

window.onload = init;

function init(){
    getAdmissionData()
}

/**
 * Hmtar in antagninsstatistiken frn en extern URL och anropar sedan funktionerna med URLens data som argument
 * 
 * 
 * @async
 * @function
 */

async function getAdmissionData(){
    try{
        const response = await fetch('https://studenter.miun.se/~mallar/dt211g/');

        if (!response.ok){
            throw new Error ('Ntverksproblem - felaktigt svar frn servern');
        }

    const data = await response.json();
    displayDiagram(data);
    displayStaple(data);
    } catch (error){
        console.error('Det uppstod ett fel:', error.message);
    }
}

/**
 * Skapar ett cirkeldiagram baserat p de mest populra programmen
 * @param {object[]} data - Array med objekt som innehller anskningsstatistiken till kurser och program.
 * @param {string} data[].type - Vad fr typ objektet r "Program" eller "kurs"
 * @param {string} data[].name - Namnet p programmet eller kursen
 * @param {string} data[].applicantsTotal - Totalt antal skande till programmet eller kursen
 */

function displayDiagram(data){
    
    const sortedData = data
        .filter(item => item.type ==='Program')
        .sort((a, b) => b.applicantsTotal - a.applicantsTotal)
        .slice(0, 6)

        const colors = ['#41afaa','#46eb4','#00a0e1','#e6a532','#d7642c','#af4b91']
        const labels = sortedData.map(item => item.name)
        const applicantsTotal = sortedData.map(item => Number(item.applicantsTotal))
        
        const diag = document.getElementById('cirkeldiagram').getContext('2d')
        new Chart(diag, {
            type:'pie',
            data:{
                labels: labels,
                datasets: [{
                    data: applicantsTotal,
                    backgroundColor: colors,
                    borderWidth: 1
                }]
            }
        })
}

/**
 * Skapar ett Stapeldiagram baserat p de mest populra kurserna
 * @param {object[]} data - Array med objekt som innehller anskningsstatistiken till kurser och program.
 * @param {string} data[].type - Vad fr typ objektet r "Program" eller "kurs"
 * @param {string} data[].name - Namnet p programmet eller kursen
 * @param {string} data[].applicantsTotal - Totalt antal skande till programmet eller kursen
 */

function displayStaple(data){
    
    const sortedData = data
        .filter(item => item.type ==='Kurs')
        .sort((a, b) => b.applicantsTotal - a.applicantsTotal)
        .slice(0, 5)

        const colors = ['#41afaa','#46eb4','#00a0e1','#e6a532','#d7642c','#af4b91']
        const labels = sortedData.map(item => item.name)
        const applicantsTotal = sortedData.map(item => Number(item.applicantsTotal))
        
        const diag = document.getElementById('stapeldiagram').getContext('2d')
        new Chart(diag, {
            type:'bar',
            data:{
                labels: labels,
                datasets: [{
                    data: applicantsTotal,
                    backgroundColor: colors,
                    borderWidth: 1
                }]
            },
            options: {
                responsive:true,
                plugins:{
                    legend:{
                        position: 'top',
                    }
                }
            }
        })
}
/**
 * Hmtar skknappen och lgger till en eventlistener fr att anropa skfunktionen
 * @constant {HTMLElement} searchBut - Skknappen i DOM
 */
const searchBut = document.getElementById('searchbut')
searchBut.addEventListener('click', searchlocation)

/**
 * Funktion fr att ska i kartan med hjlp av Openstreetmaps nominatim API
 * @async
 * @function searchlocation
 */

async function searchlocation(){
    let location = document.getElementById('locationinput').value;
    /**
     * Hmtar anvndarens inmatning och lgger det i en variabel
     * @type {string}
     */
    try{
        /**
         * Hmtar API baserat p vad anvndaren matat in
         */
        const response = await fetch(`https://nominatim.openstreetmap.org/search?format=json&q=${location}`);

        if (!response.ok){
            throw new Error ('Ntverksproblem - felaktigt svar frn servern');
        }
    /**
     * omvandlar API-svaret till JSON
     */
    const data = await response.json();
    /**
     * Latitude frn platsen, parsefloat fr att parsea strngen till ett nummer
     * @type {number}
     */
    let lat = parseFloat(data[0].lat)
    let lon = parseFloat(data[0].lon)
    /**
     * iframe elementet dr kartan visas
     * @constant {HTMLElement} map - iframe i DOM
     */
    let map = document.getElementById('mapcont')
    /**
     * Url som visarr kartan i iframen beserat p det hmtade korodinaterna
     * @constant {string} map.src - OpenStreetMap inbddad kart url
     */
    map.src = `https://www.openstreetmap.org/export/embed.html?bbox=${lon-0.01},${lat-0.01},${lon+0.01},${lat+0.01}&layer=mapnik&marker=${lat},${lon}`

    } catch (error){
        console.error('Det uppstod ett fel:', error.message);

    }
}